$inputBg: #202020;

.form__label {
  @include visually-hidden;
}

.form__input {
  margin-bottom: 20px;

  input,
  textarea {
    border-radius: 10px;
    background-color: $inputBg;
    width: 100%;
    padding: 16px 10px 16px 30px;
    display: block;
    box-sizing: border-box;
    border: none;
    box-shadow: none;
    resize: none;
    color: $white;
    font-size: 20px;
    font-family: 'Raleway';
    line-height: 1.4;

    &::placeholder {
      color: $white;
      opacity: 1;
    }
  }

  textarea {
    min-height: 200px;
  }
}

.form__button {
  border-radius: 10px;
  background-color: $second;
  width: 100%;
  appearance: none;
  border: none;
  box-shadow: none;
  cursor: pointer;
  color: $white;
  text-transform: uppercase;
  font-family: 'Raleway';
  font-size: 20px;
  font-weight: 700;
  line-height: 1.4;
  text-align: center;
  padding: 16px 0;
  transition-property: background-color;
  transition-duration: 500ms;

  &:hover,
  &:active {
    background-color: darken($second, 15%);
  }
}
