$listColor: #959595;

.list {
  font-size: 14px;
  color: $listColor;
  line-height: 1.286;
  list-style: none;
  padding: 0;
}

.list__element {
  margin: 15px 0;
  padding-left: 55px;
  position: relative;

  &::before {
    content: '';
    background-color: $second;
    position: absolute;
    left: 23px;
    top: 50%;
    transform: translateY(-50%);
    width: 8px;
    height: 2px;
  }
}
